$toggle-bar-height: 2px;

#wpadminbar {
	.pyxis-navbar-positioning-fixed & {
		position: fixed;
	}
}

#pyxis-mobile-header {
	display: flex;
	align-items: center;
	position: absolute;
	padding: 10px;
	width: 100%;
	top: 0;
	left: 0;
	background-color: #000000;
	box-sizing: border-box;
	z-index: 9999;

	.admin-bar & {
		top: 46px;
		
		@media (min-width: 783px) {
			top: 32px;
		}
	}

	.pyxis-mobile-main {
		flex: 0 0 100%;
		text-align: center;
		color: #ffffff;
		font-size: 16px;
	}

	.pyxis-mobile-toggle {
		cursor: pointer;
		position: absolute;
		top: auto;
		left: 15px;
		padding: 0;
		margin: 0;
		border: none;

		&:active,
		&:focus {
			top: auto;
		}
	}

	.pyxis-navbar-positioning-absolute & {
		position: absolute;
	}

	.pyxis-navbar-positioning-fixed & {
		position: fixed;
	}
}

/* Toggle Location */
.pyxis-mobile-toggle {
	.pyxis-toggle-location-right & {
		left: auto;
		right: 15px;
	}
}

/* Toggle Type */
.pyxis-mobile-toggle {
	.pyxis-toggle-type-hamburger & {
		width: 32px;
		height: 32px;
		background-color: transparent;
		text-align: center;

		span {
			display: none;
		}

		.pyxis-mobile-toggle-bars {
			position: relative;
			height: $toggle-bar-height;
			width: 24px;
			background-color: #fff;
			margin: 0 auto;

			&:before,
			&:after {
				position: absolute;
				content: '';
				width: 100%;
				height: $toggle-bar-height;
				background-color: #ffffff;
				top: 0;
				left: 0;
			}

			&:before {
				transform: translateY(-8px);
			}

			&:after {
				transform: translateY(8px);
			}

			.pyxis-mobile-active & {
				background-color: transparent;

				&:before {
					transform: translateY(0) rotate(-45deg);
				}

				&:after {
					transform: translateY(0) rotate(45deg);
				}
			}
		}
	}

	.pyxis-toggle-type-button & {
		width: auto;
		padding: 5px 10px;
		background-color: blue;
	}
}

.pyxis-mobile-overlay {
	display: none;
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background-color: rgba(0,0,0,0.8);
	z-index: 9999;

	.pyxis-mobile-active & {
		display: block;
	}
}

#pyxis-mobile-menu-container {
	position: fixed;
	overflow: scroll;
	top: 0;
	right: -100%;
	width: 100%;
	height: 100vh;
	z-index: 99999;
	transition: all 0.5s cubic-bezier(1.000, 0.000, 0.000, 1.000);
	background-color: #000000;
	padding: 15px 0 70px;

	.admin-bar & {
		top: 46px;

		@media (min-width: 783px) {
			top: 32px;
		}
	}

	.pyxis-mobile-close {
		display: flex;
		flex-flow: column nowrap;
		justify-content: center;
		flex: 0 0 32px;
		cursor: pointer;
		position: relative;
		width: 12px;
		height: 12px;
		background-color: transparent;
		padding: 0;
		margin: 0 0 15px 0;
		border: none;

		.pyxis-mobile-toggle-bars {
			position: relative;
			height: $toggle-bar-height;
			width: 100%;
			background: transparent;

			&:before,
			&:after {
				position: absolute;
				content: '';
				width: 100%;
				height: $toggle-bar-height;
				background: #fff;
				top: 0;
				left: 0;
			}

			&:before {
				transform: translateY(0) rotate(-45deg);
			}

			&:after {
				transform: translateY(0) rotate(45deg);
			}
		}
	}

	.pyxis-mobile-active & {
		right: 0;
		transition: all 0.5s cubic-bezier(1.000, 0.000, 0.000, 1.000);
	}

	.pyxis-flyout-style-default & {
		width: 250px;
	}

	.pyxis-flyout-style-full & {
		width: 100%;
	}

	.pyxis-toggle-location-left & {
		left: -100%;
		right: auto;
	}

	.pyxis-mobile-active.pyxis-toggle-location-left & {
		left: 0;
	}

	.pyxis-mobile-menu-wrap {
		width: 80%;
		margin: 0 auto;
	}

	.pyxis-mobile-no-menu {
		h4 {
			color: #fff;
		}

		p {
			font-size: 16px;
			line-height: 1.3;
			margin: 10px 0;
		}
	}
}

/* Menu */
#pyxis-mobile-menu-container {
	ul.menu {
		list-style-type: none;
		margin: 0;
		padding: 0;

		li {
			a {
				display: block;
				padding: 5px 0;
				color: #ffffff;
			}
		}
	}

	ul.sub-menu {
		display: none;
		list-style-type: none;
		margin: 0;
		padding: 0 0 0 15px;
	}

	.menu-link-has-children {
		display: flex;

		a {
			flex: 1 0 auto;

			.pyxis-nav-layout-flyout-full & {
				flex: 0 1 auto;
			}
		}
	}

	.pyxis-submenu-toggle {
		position: relative;
		flex: 0 0 25px;
		cursor: pointer;

		&.open {
			&:after {
				transform: translate(-50%, -80%) rotate(-135deg);
				transition: transform 0.5s ease-in-out;
			}
		}

		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			display: block;
			width: 6px;
			height: 6px;
			border-style: solid;
			border-width: 0 2px 2px 0;
			transform-origin: center;
			transform: translate(-50%, -80%) rotate(45deg);
			transition: transform 0.5s ease-in-out;
		}
	}
}